.toggle-button {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	height: 24px;
	width: 30px;
	background: transparent;
	border: none;
	padding: 0;
	box-sizing: border-box;
}

.toggle-button:focus {
	outline: none;
	cursor: pointer;
}
.toggle-button-line {
	widows: 30px;
	height: 2px;
	background: #fb3a1c ;
}

.homebox{
	color: white;
	background-color: #fb3a1c;
	font-size: 28px;
	transform: translateX(-40%); 
	font-size: 1rem;
	font-weight: 900;
	width: 55px;
	padding: 4px;
	border-radius: 0.3rem;
	display:flex;
	transition: transform 1s ease-out;
}
.homeboxdown{
	color: white;
	background-color: #fb3a1c;
	font-size: 28px;
	transform: translateX(-40%); 
	font-size: 1rem;
	font-weight: 900;
	width: 55px;
	padding: 4px;
	border-radius: 0.3rem;
	display:flex;
	transition: transform 1s ease-out;
}
.homeboxLeft{
	color: white;
	background-color: #fb3a1c;
	font-size: 28px;
	transform: translateX(5%); 
	font-size: 1rem;
	font-weight: 900;
	width: 80px;
	padding: 4px;
	border-radius: 0.3rem;
	display:flex;
	transition: transform 1s ease-out;
}
/* Create two equal columns that floats next to each other */
.column {
	float: left;
	width: 50%;
	padding: 0.2rem;
	display:flex;
  }
  
  /* Clear floats after the columns */
  .row:after {
	content: "";
	display: table;
	clear: both;
  }